@import "variables";

button {
  background: $interfaceColor;
  color: white;
  width: 100%;
  height: 100%;
  border-radius: 5px;
  border: none;
  font-size: 16px;
  font-family: "PoppinsRegular", sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
}

.disabled {
  opacity: 0.3;
  color: white;
  font-weight: bold;
  cursor: default;
  transition: 0.2s;
}

.disabled-cart {
  color: $interfaceColor;
  font-weight: bold;
  background: #fafafa;
  transition: 0.2s;
  cursor: pointer;
}

.disabled-cart:hover{
  filter: brightness(95%);
}

.disabled-cart:active{
  filter: brightness(90%);
}

.active-button {
  background: $interfaceColor;
  color: white;
  font-weight: bold;
  cursor: pointer;
  transition: 0.2s;
}

.pending-button {
  background: $interfaceColor;
  color: white;
  font-weight: bold;
  cursor: pointer;
  transition: 0.2s;
}

.active-button:hover {
  background: #00b63b;
}

.active-button:active {
  background: #00a133;
}

.sign-block {
  min-width: 100px;
  width: 100%;
  height: 45px;
}

.register-button {
  background: rgb(50 59 67);
  color: white;
  font-weight: bold;
  cursor: pointer;
  transition: 0.2s;
}

.register-button:hover {
  background: rgb(26, 28, 30);
}




