.product-shares {
  min-width: 500px;
  border-radius: 5px;
  flex-basis: 45%;
  flex-shrink: 1;
  flex-grow: 1;
  margin: 10px;
  display: flex;
  border: 1px solid #a5a5a5;
  position: relative;
  transition: 0.3s;
}

.product-shares:hover {
  -webkit-box-shadow: 0px 0px 10px 0px rgba(66, 68, 90, 1);
  -moz-box-shadow: 0px 0px 10px 0px rgba(66, 68, 90, 1);
  box-shadow: 0px 0px 10px 0px rgba(66, 68, 90, 1);
  border: 1px solid transparent;
}

.product-block {
  display: flex;
  flex-direction: column;
  width: 300px;
  border: 1px solid #a5a5a5;
  border-radius: 5px;
  flex-shrink: 1;
  flex-grow: 1;
  margin: 10px;
  position: relative;
  transition: 0.3s;
  cursor: pointer;
}

.product-block:hover {
  -webkit-box-shadow: 0px 0px 10px 0px rgba(66, 68, 90, 1);
  -moz-box-shadow: 0px 0px 10px 0px rgba(66, 68, 90, 1);
  box-shadow: 0px 0px 10px 0px rgba(66, 68, 90, 1);
  border: 1px solid transparent;
}

.product-link {
  z-index: 10;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  cursor: pointer;
}

.product-img {
  width: auto;
  height: 250px;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
  border-bottom: 1px solid #a5a5a5;

  img {
    width: 100%;
    height: 100%;
    object-fit: contain;
  }
}

.shares-img {
  height: 250px;
  width: 50%;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
  border-right: 1px solid #a5a5a5;

  img {
    width: auto;
    height: 100%;
    object-fit: cover;
  }
}

.product-active-shares {
  padding: 10px;
  display: flex;
  flex-direction: column;
  width: calc(100% / 2);
  height: 250px;
  justify-content: space-between;
}

.product-active {
  padding: 10px;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 300px;
  justify-content: space-between;
}

.product-info {
  width: 100%;
  height: 150px;
  font-family: "PoppinsMedium", "sans-serif";
}

.product-price {
  display: flex;
  align-items: center;
  justify-content: space-between;

  .price-block {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  span {
    font-weight: bold;
    font-size: 25px;
    text-decoration-color: black;
  }

  strong {
    color: white;
    padding: 5px;
    border-radius: 5px;
  }

  span:nth-child(1) {
    margin-right: 15px;
  }
}

.product-name {
  margin-top: 30px;
  display: flex;

  span {
    font-size: 18px;
    font-weight: bold;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
}

.product-description {
  margin-top: 10px;
  display: flex;
  width: 100%;

  .text-block {
    width: 80%;
    span {
      color: #454545;
    }

    .line-description{
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
      overflow: hidden;
      max-height: 3.2em;
    }
  }
}

.store {
  width: auto;
  display: flex;
  justify-content: center;
  align-items: center;

  .store-img {
    width: 70px;
    height: 70px;
    border-radius: 100px;
    border: 2px solid #d1d1d1;

    img {
      border-radius: 100px;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}


.product-button {
  position: relative;
  height: 45px;
  width: 100%;

  button {
    position: absolute;
    z-index: 10;
    top: 0;
  }
}

.percentage {
  background: red;
  position: absolute;
  padding: 5px;
  left: 10px;
  top: 10px;
  color: white;
  border-radius: 5px;

  span {
    font-size: 20px;
    font-family: "PoppinsMedium", "sans-serif";
  }
}

.loading-gradient-p {
  border-radius: 5px;
  opacity: 0.7;
  width: 100%;
  height: 100%;
  background: rgb(255, 255, 255);
  background: linear-gradient(90deg, rgba(255, 255, 255, 1) 0%, rgba(194, 194, 194, 1) 35%, rgba(230, 230, 230, 1) 100%);
  background-size: 400%;
  animation: gradient 2s infinite linear;
}

.loading-gradient-endDate {
  border-radius: 5px;
  opacity: 0.7;
  background: rgb(255, 255, 255);
  background: linear-gradient(90deg, rgb(255, 0, 0) 0%, rgba(255, 195, 0, 0.9) 35%, rgb(255, 55, 55) 100%);
  background-size: 400%;
  animation: gradient 4s infinite linear;
}

.loading-gradient-review {
  border-radius: 5px;
  opacity: 0.7;
  background: rgb(255, 255, 255);
  background: linear-gradient(90deg, rgba(255, 195, 0, 0.9) 0%, rgba(4, 224, 21, 0.9) 35%, rgba(136, 224, 4, 0.9) 100%);
  background-size: 400%;
  animation: gradient 4s infinite linear;
}

@keyframes gradient {
  0% {
    background-position: 80% 0%;
  }
  50% {
    background-position: 20% 100%;
  }
  100% {
    background-position: 80% 0%;
  }
}

.loading-span {
  width: 100%;
}

.loading-img-p {
  width: 100%;
  height: 250px;
  padding: 10px;
}

.loading-img-s {
  width: 50%;
  height: 100%;
  padding: 10px;
}
